
.thumbnail a >img,
.thumbnail>img {
    width: 550px;
}
.video {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    padding: 0;
    padding-top: 25px;
}
.player {
        width: 55%;
}
.cards {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        width: 45%;
        padding-top: 26px;
}
.card {
            height: 100px;
            margin: 10px 15px;
            background: #eee;
            border: 2px solid #555;
            cursor: pointer;
}
.jw-controlbar {
            /*display: none;*/
}
.jw-controlbar-center-group .jw-slider-time, 
        .jw-controlbar-center-group .jw-slider-container, 
        .jw-controlbar-center-group .jw-rail,
        .jw-controlbar-center-group .jw-slider-horizontal .jw-progress,
        .jw-controlbar-center-group .jw-buffer {
            height: 100% !important;
}
.jw-controlbar-center-group .jw-knob {
            background-color: #202020;
            border: 0.12em solid #fff;
            font-size: 3em;
            top: 10%;
}
.jw-dock {
            margin-top: 35%;
            margin-left: 10%;
}
.jw-dock-button {
                font-size: 3.5em;
}
.jw-nextup-container {
            opacity: 1;
            visibility: visible;
            padding: 0;
            margin: 0;
}
.jw-nextup { display: none;
}
.annomenu {
                border: 1px solid #fff;
                color: #fff;
                background: rgba(33,33,33,.8);
                height: 90px;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
}
.annomenu-option {
                    width: 25%;
                    border-right: 1px solid #fff;
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    -webkit-box-pack: center;
                        -ms-flex-pack: center;
                            justify-content: center;
                    -webkit-box-align: center;
                        -ms-flex-align: center;
                            align-items: center;
}



