
.head {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #A90931;
  color: #FFF;
}
.head__logo {
      padding-top: 20px;
      margin-left: 120px;
      font-size: 24px;
      font-weight: bold;
}
.head__logo i {
            color: #FFF;
            padding-right: 5px;
}
.head__nav {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      color: #FFF;
      margin-left: 60px;
      padding:0;
}
.head__nav-item {
            margin-left: 20px;
            padding: 10px 16px;
}
.head__nav-item-active {
  background-color: #8F082A;
  border-radius: 20px;
  padding: 10px 16px;
}



